@import url('https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:wght@500;600&family=Damion&family=Lato:wght@300;400&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #333;
  font-family: 'Lato', sans-serif;
}

h1, h2{
  font-family: 'Be Vietnam Pro', sans-serif;
}
p{
  color: rgb(65, 64, 64);
}

/* 
////////////////////////////////////////////////
HEADER & NAV
////////////////////////////////////////////////
*/
header.home{
  position: fixed;
  width: 100%;
  background-color: rgba(2, 35, 61, 0);
  padding: 0 20px;
  z-index: 9;
}
header{
  position: static;
  width: 100%;
  background-color: rgba(2, 35, 61, 1);
  padding: 0 20px;
}
header nav {
  display: grid;
  justify-content: space-between;
  align-items: center;
  max-width: 980px;
  height: 140px;
  margin: 0 auto;
  padding: 48px 0 50px 0;
}
header nav ul{
  display: flex;
}
header nav ul li{
  list-style: none;
  padding: 10px;
}
header nav ul li a{
  font-weight: 300;
  color: white;
  text-decoration: none;
  font-family: 'Lato', sans-serif;
}
.brand{
  position: absolute;
  text-align: center;
  max-width: 200px;
  margin: 0 auto;
  left: 0;
  right: 0;
  top: 20px;
}
.brand .logo img{
  max-width: 150px;
}
.toggle-nav{
  display: none;
}
@media only screen and (max-width: 768px) {
  header, header.home{
    position: static;
    width: 100%;
    background-color: rgba(2, 35, 61, 1);
    padding: 0 20px;
  }
  nav.hidden{
    display: none;
  }
  nav.hidden.showmenu{
    display: block;
  }
  .brand{
    position: static;
    padding: 20px;
  }
  .brand .logo img{
    max-width: 120px;
  }
  .toggle-nav{
    display: block;
    position: absolute;
    color: white;
    font-size: 36px;
    top: 40px;
    left: 30px;
    padding: 0;
    margin: 0;
    line-height: 0;
    cursor: pointer;
  }

}
/* 
////////////////////////////////////////////////
BANNER
////////////////////////////////////////////////
*/

.banner_top img{
  width: 100%;
}
@media only screen and (max-width: 768px) {
  .banner_top img{
    display: none;
  }
}
/* 
////////////////////////////////////////////////
HOME : Blogs
////////////////////////////////////////////////
*/
.recent_posts{
  background: white;
  max-width: 860px;
  margin: 0 auto;
  padding: 40px 30px;
}
.entry_title{
  text-align: center;
  margin-bottom: 20px;
  margin-top: 0;
  font-family: 'Damion', cursive;
  font-size: 48px;
  color: #193142;
}
.blog-list{
  display: grid;
  justify-content: center;
  gap: 40px;
  grid-template-columns: repeat(auto-fill, minmax(326px, 1fr) );
}
.blog-preview img{
  width: 100%;
}

.blog-preview{
  background: white;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.45);
  padding: 20px;
} 

.post_details{
  padding: 10px;
}
.post_details h2{
  color: #f2792f;
  padding-bottom: 10px;
  padding-top: 10px;
}
.post_details p{
  line-height: 21px;
}
.id_link{
  text-decoration: none;
}
@media only screen and (max-width: 768px) {
  .blog-list{
    grid-template-columns: repeat(auto-fill, minmax(340px, 1fr) );
  }
}
/* 
////////////////////////////////////////////////
HOME : Book
////////////////////////////////////////////////
*/


.book_now{
  position: relative;
}
.img_bottom img{
  width: 100%;
}
.book_button{
  max-width: 980px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  margin: 0 auto;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
}

.book_now p{
  font-family: 'Damion', cursive;
  font-size: 26px;
  text-transform: capitalize;
  color: #efe5da;
  transform: rotate(-7deg);
  text-shadow: 5px 5px 8px rgba(0, 0, 0, 0.85);
  max-width: 325px;
  margin: 0 auto;
  margin-bottom: 20px;
}

@media only screen and (min-width: 600px) {
  .book_now p{
    font-size: 32px;
    max-width: 580px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
}
@media only screen and (min-width: 768px) {
  .book_now p{
    font-size: 36px;
  }
}
@media only screen and (min-width: 992px) {
  .book_now p{
    font-size: 44px;
  }
}

.book_now a{
  font-size: 24px;
  text-transform: lowercase;
  background: #f2792f;
  color: #efe5da;
  padding: 5px 25px;
  text-decoration: none;
}
.book_now a:hover{
  background: rgb(222, 104, 2);
}

/* 
////////////////////////////////////////////////
FOOTER
////////////////////////////////////////////////
*/
footer{
  background: #10212d;
  position: relative;
}
.footer_wrap{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 980px;
  margin: 0 auto;
  column-gap: 1px;
  margin-top: -4px;
  background: #142938;
  border-left: 1px #142938 solid;
  border-right: 1px #142938 solid;
}
.section{
  flex-grow: 1;
  background: #10212d;
  padding: 40px 20px; 
}

/* 
////////////////////////////////////////////////
FOOTER: Menu
////////////////////////////////////////////////
*/

.footer_menu ul{
  padding-left: 20px;
}
.footer_menu ul li {
  padding-bottom: 20px;
  color: #5a7d95;
}
.footer_menu ul li a{
  color: #5a7d95;
  text-decoration: none;
}
.footer_menu ul li a:hover{
  text-decoration: underline;
}

/* 
////////////////////////////////////////////////
FOOTER: Menu
////////////////////////////////////////////////
*/

.social_footer ul{
  padding-left: 20px;
}

.social_footer ul li {
  color: #5a7d95;
  list-style: none;
  display: inline;
  margin-right: 20px;
}
.social_footer ul p{
  padding-bottom: 10px;
  color: #5a7d95;
}